.project {
  display: block;
  margin: 20px 5px;
  width: 320px;
  height: 500px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  -webkit-box-shadow: 6px 10px 20px rgba(0, 0, 0, 0.1);
          box-shadow: 6px 10px 20px rgba(0, 0, 0, 0.1);
  background-color: white;
  text-align: center;
}

.project:hover {
  -webkit-box-shadow: 6px 20px 20px rgba(0, 0, 0, 0.3);
          box-shadow: 6px 20px 20px rgba(0, 0, 0, 0.3);
  -webkit-transform: translate(0, -20px);
      -ms-transform: translate(0, -20px);
          transform: translate(0, -20px);
}

/* Prevents hover unhover loop while cursor is on bottom edge */
.project:hover::before {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 20px;
}

.project img {
  width: 100%;
}

.project__link {
  display: inline-block;
  width: 40%;
  padding: 0.5em;
  margin: 10px 5px;
  color: black;
  background-clip: text;
  border: 5px solid rgba(255, 130, 130, 1);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
}

.project__link:hover{
  border-radius: 30px;

}

.screenshot {
  max-height: 150px;
  overflow: hidden;
}

.project__icon {
  height: 1em;
  vertical-align: sub;
  margin: 2px;
}


