.header {
  background-color: #222;
  color: white;
  background: url('./images/thomas-verbruggen.jpg') center no-repeat;
  background-size: cover; 
}

.title--main {
  padding-top: 50px;
}

.call-to-action{
  background: green;
  color: white;
  /* height: 2em; */
  width: 10em;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  margin: 2em auto;
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bolder;
}

.list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 0;
  padding: 10px;
}

.carousel {
  overflow: hidden;
  width: 100%;
  margin: 0;
  padding: 0;
}

.carousel__item {
  text-align: center;
  padding: 10px;
  max-width: 200px;
}

.title{
  text-align: center;
  font-size: 1.5em;
}

.center-text{
  text-align: center;
}

strong::before {
  white-space: pre;
  content: "\A"
}

.header__paragraph{
  line-height: 1.5em;
}


