.menu {
    z-index: 5;
    padding: 0;
    margin: 0;
    position: fixed;
    width: 100%;
    top: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;

}

.menu li, .menu a {
    display: block;
    height: 50px;
}

.menu li{
    width: 33.33%;
    padding: .5em 0;   
    -webkit-transition: border-color .5s;   
    -o-transition: border-color .5s;   
    transition: border-color .5s;
}

.menu li a {
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    text-align: center;
    padding: calc(50px - 2.8em) 0;

}

@media (max-width: 500px) {
    .menu li, .menu a {
        font-size: .9em;
    }
}

.menu li {
    background-color: #222;
    border-bottom: 5px solid transparent;
}

.menu li:hover {
    border-color: white;
}


